import Vue from 'vue';
import Router from 'vue-router';
import 'nprogress/nprogress.css';
import NProgress from 'nprogress';
import store from '@/store.js';

Vue.use(Router);

NProgress.configure({ showSpinner: false });

const router = new Router({
  routes: [
    {
      path: '',
      component: () => import('@/components/content.vue'),
      children: [
        {
          path: '/',
          redirect: '/order-list',
        },
        {
          path: '/order-list',
          name: 'order-list',
          component: () => import('@/views/order-list.vue'),
          meta: {
            title: '订单列表',
          },
        },
        {
          path: '/order-detail/:id',
          name: 'order-detail',
          component: () => import('@/views/order-detail.vue'),
          meta: {
            title: '订单详情',
          },
        },
        {
          path: '/trans-flow',
          name: 'trans-flow',
          component: () => import('@/views/trans-flow.vue'),
          meta: {
            title: '交易流水',
          },
        },
      ],
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/login.vue'),
      meta: {
        title: '登录',
      },
    },

    {
      path: '/404',
      name: '404',
      component: () => import('@/views/404.vue'),
    },
    {
      path: '*',
      redirect: '/404',
    },
  ],
});

const whiteList = ['/login', '/404']; // 不重定向白名单
router.beforeEach((to, from, next) => {
  NProgress.start();
  const meta = to.meta || {};
  document.title = meta.title || '维信分期宝';
  if (whiteList.includes(to.path)) {
    next();
  } else if (!store.state.userInfo.token) {
    // 跳往登录页面，获取TOKEN
    next('/login');
  } else if (!store.state.userInfo.username) {
    console.log('a');
  } else {
    next();
  }
});

router.afterEach((to, from) => {
  _waq.push(['send', {
    type: 'URL_CHANGE',
    logInfo: {
      prevUrl: `${window.location.origin}${window.location.pathname}${window.location.search}#${from.path}`,
      newURL: `${window.location.origin}${window.location.pathname}${window.location.search}#${to.path}`,
    },
  }]);
  NProgress.done();
  NProgress.remove();
});

export default router;
