import Vue from 'vue';
import Vuex from 'vuex';
import { login, getMenu } from '@/api/index.js';

Vue.use(Vuex);

const session = JSON.parse(window.sessionStorage.getItem('USERINFO')) || {};

export default new Vuex.Store({
  state: {
    isLoading: false, // 请求正在加载
    userInfo: {
      username: session.username || '',
      token: session.token || '',
    },
    menuObj: {
      订单管理: false,
      订单列表: false,
      交易流水: false,
      交易流水列表: false,
    },
  },
  mutations: {
    SHOW_LOADING(state) {
      state.isLoading = true;
    },
    CLOSE_LOADING(state) {
      state.isLoading = false;
    },
    UPDATE_USERINFO(state, result) {
      Object.assign(state.userInfo, result);
      window.sessionStorage.setItem('USERINFO', JSON.stringify(state.userInfo));
    },
    UPDATE_MENULIST(state, result) {
      for (let i = 0; i < result.length; i += 1) {
        const item = result[i];
        if (item.name) {
          state.menuObj[item.name] = true;
        }
        for (let j = 0; j < item.sublevel.length; j += 1) {
          const subItem = item.sublevel[j];
          if (subItem.name) {
            state.menuObj[subItem.name] = true;
          }
        }
      }
    },
  },
  actions: {
    LOGIN({ commit, dispatch }, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo.username.trim(), userInfo.password.trim(), userInfo.verificationCode.trim())
          .then((result) => {
            _waq.push(['_setUserId', userInfo.username]);
            commit('UPDATE_USERINFO', {
              token: result,
              username: userInfo.username,
            });
            dispatch('NAVMENU').finally(console.log);
            resolve('ok');
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    LOGOUT({ commit }) {
      return new Promise((resolve) => {
        window.sessionStorage.removeItem('USERINFO');
        commit('UPDATE_USERINFO', {
          token: '',
          username: '',
        });
        resolve('ok');
      });
    },
    NAVMENU({ commit }) {
      return new Promise((resolve, reject) => {
        getMenu().then((result) => {
          console.log(result);
          if (Array.isArray(result)) {
            commit('UPDATE_MENULIST', result);
          }
          resolve(result);
        }).catch(err => reject(err));
      });
    },
  },
});
