import Vue from 'vue';

const filters = {
  orderStatus(str) {
    let val = str;
    switch (val) {
      case '000000':
        val = '续费中';
        break;
      case '100100':
        val = '订单取消';
        break;
      case '100200':
        val = '待支付首付';
        break;
      case '100201':
        val = '续费失败';
        break;
      case '100300':
        val = '已结清';
        break;
      case '100301':
        val = '提前结清';
        break;
      default:
        break;
    }
    return val;
  },
  orderType(str) {
    let val = str;
    switch (val) {
      case '600101':
        val = '连续续费';
        break;
      case '600102':
        val = '分期';
        break;
      default:
        break;
    }
    return val;
  },
  billType(str) {
    let val = str;
    switch (val) {
      case '600301':
        val = '首付款';
        break;
      case '600303':
        val = '分期付款';
        break;
      case '600302':
        val = '连续续费';
        break;
      default:
        break;
    }
    return val;
  },
  payType(str) {
    let val = str;
    switch (val) {
      case '600401':
        val = '主动支付';
        break;
      case '600402':
        val = '正常扣款';
        break;
      default:
        break;
    }
    return val;
  },
  renewStatus(str) {
    let val = str;
    switch (val) {
      case '000000':
        val = '交易成功';
        break;
      case '100101':
        val = '扣款中';
        break;
      case '100201':
        val = '交易失败';
        break;
      case '100000':
        val = '取消交易';
        break;
      default:
        break;
    }
    return val;
  },
};

Object.keys(filters).forEach(k => Vue.filter(k, filters[k]));
