import axios from 'axios';
import Vue from 'vue';
import { Message } from 'element-ui';
import store from '@/store.js';
import { list2, list3 } from '@/assets/js/connect.list.js';

const baseURL = process.env.VUE_APP_BASE_API;
const reg = new RegExp(baseURL);

const { CancelToken } = axios;
const pending = []; // 声明一个数组用于存储每个ajax请求的取消函数和ajax标识
const removePending = (config, f) => {
  const url = config.url.split('?')[0].replace(reg, '');
  const i = pending.indexOf(url);
  if (i > -1) {
    if (typeof f === 'function') {
      f();
    } else {
      pending.splice(i, 1);
    }
  } else {
    pending.push(url);
  }
};

const request = axios.create({
  baseURL,
  timeout: 20000,
});

request.defaults.retry = 2; // 接口失败，允许重复次数
request.defaults.retryDelay = 1000; // 再次发送，间隔时间

// 加载进度设置
let requestCount = 0;
const handleRequestLoading = () => {
  requestCount += 1;
  if (requestCount) {
    store.commit('SHOW_LOADING');
  }
};
const handleResponseLoading = () => {
  if (requestCount > 0) {
    requestCount -= 1;
    if (!requestCount) {
      store.commit('CLOSE_LOADING');
    }
  }
};

request.interceptors.request.use(
  (config) => {
    const url = config.url.split('?')[0].replace(reg, '');
    // 判断该请求需不需要展示loading
    handleRequestLoading();
    // 判断该请求是不是可以重复发送；
    const isCanRepeat = list2.find(item => item.indexOf(url) > -1);
    if (!isCanRepeat) {
      // 取消请求
      config.cancelToken = new CancelToken((c) => {
        removePending(config, c);
      });
    }
    // 判断该请求是不是不需要添加token；
    const isNoNeed = list3.find(item => item.indexOf(url) > -1);
    if (!isNoNeed) {
      config.headers.Authorization = store.state.userInfo.token;
    }

    return config;
  },
  (error) => {
    console.log(error.config);
    return Promise.reject(error);
  },
);

// Add a response interceptor
request.interceptors.response.use(
  (response) => {
    handleResponseLoading();
    removePending(response.config);
    const res = response.data;
    // 接口状态异常
    if (res.code !== '000000') {
      // 未登录
      if (res.code === '100002') {
        Message({
          type: 'success',
          message: res.msg,
        });
        return Promise.reject(new Error('not login'));
      }
      // 不需要在拦截器提示的，params参数里有interceptorHint信息，且等于needless
      if (
        !(
          response.config.params
          && response.config.params.interceptorHint === 'needless'
        )
      ) {
        Message({
          type: 'warning',
          message: res.msg,
        });
      }
      return Promise.reject(new Error('error'));
    }
    return res.data;
  },
  (error) => {
    // 接口重复发送
    if (axios.isCancel(error)) {
      handleResponseLoading();
      console.log('Request canceled');
    } else {
      const { config } = error;
      removePending(config);
      if (!config || !config.retry) {
        handleResponseLoading();
        Message({
          type: 'error',
          message: error.message,
        });
        return Promise.reject(error);
      }
      // 接口失败次数
      config.retryCount = config.retry || 0;
      if (config.retryCount >= config.retry) {
        handleResponseLoading();
        Message({
          type: 'error',
          message: error.message,
        });
        return Promise.reject(error);
      }
      config.retryCount += 1;
      // 再次发送
      const backoff = new Promise((resolve) => {
        setTimeout(() => {
          handleResponseLoading();
          Vue.nextTick(() => {
            resolve();
          });
        }, config.delay || 1);
      });

      return backoff.then(() => request(config));
    }
    return Promise.reject(error);
  },
);

export default request;
